




















import { Component, Prop, Vue } from 'vue-property-decorator';
import { GameData } from '@/globalInterface';

@Component({
  filters: {
    getTextList(arr: GameData[]) {
      if (arr) {
        return arr.map((x: GameData) => {
          const gameChild = (x.children || []) as GameData[];
          return {
            text: x.text,
            children: gameChild.map((y: GameData) => y.text)
          };
        });
      }
      return [];
    }
  }
})
export default class PickerTwo extends Vue{
  @Prop({type: Array, default: () => []}) public readonly dataList!: GameData[];
  @Prop({type: Boolean, default: false}) public readonly isGetFullId!: boolean;
  @Prop({type: Boolean, default: false}) public readonly isCombineText!: boolean;
  private showPicker?: boolean = false;
  private selectedId: number[] = [0, 0]; 
  private getText(): string {
    let textOut = '';
    if (this.dataList) {
      const selectedType: GameData[] = this.dataList[this.selectedId[0]].children as GameData[];
      if (this.isCombineText) {
        textOut += this.dataList[this.selectedId[0]].text;
      }
      if (selectedType) {
        const selectedChildren: GameData = selectedType[this.selectedId[1]];
        textOut += selectedChildren.text;
        return textOut;
      }
    }
    return '';
  }
  private emitData(data: GameData) {
    this.$emit('index-change', data);
  }
  private togglePicker() {
    this.showPicker = !this.showPicker;
  }
  private onConfirm(value: string, index: number[] ) {
    this.selectedId = index;
    this.togglePicker();
    if (this.isGetFullId) {
      this.emitData({
        id: index,
        text: value[0]
      });
    } else {
      const data = this.dataList || [];
      const selectedType: GameData[] = data[index[0]].children as GameData[];
      const selectedChildren: GameData = selectedType[index[1]];
      if (selectedChildren) {
        this.emitData(selectedChildren);
      }
    }
  }
}
