




















import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import RecommendPlanSelector from './RecommendPlanSelector.vue';

@Component({
  components: {
    RecommendPlanSelector
  }
})
export default class RecommendTable extends Vue {
  @Prop({type: Number, default: -1}) public readonly selectedId?: number;
  private rank: number | string = 0;
  private type: number | string = 0;
  private order: number | string = 0;
  private lotteryId: number | string = 11;
  private tableData?: {[T: string]: number | string | Array<string | number>}[] = [
    {
      id: 10,
      proId: 12,
      username: '爱理不理',
      drawString: '2,7,3,8,6,4,5',
      yards: 7,
      correctRate: '98.00%',
      connected: 16,
    }
  ]
  private asignFilter(newValues: {[T: string]: string | number}) {
    this.rank = newValues.rank;
    this.type = newValues.type;
    this.order = newValues.order;
    this.callApi();
  }
  private callApi() {
    console.log('LotteryTwoSideTable', {
      rank: this.rank,
      type: this.type,
      order: this.order
    });
    // CallAPI
    // Change tableData
  }
  private goInnerPage(proId: number | string ) {
    const params: any = {
      rank: this.rank,
      type: this.type,
      order: this.order,
      proId: proId,
      gameId: this.lotteryId
    }
    this.$router.push({name: 'proplan', params})
  }
}
