











import { Component, Vue, Prop } from 'vue-property-decorator';
import PickerOne from '@/application/Common/View/CommonPickerOne.vue';
import PickerTwo from '@/application/Common/View/CommonPickerTwo.vue';
import { GameData } from '@/globalInterface';

@Component({
  components: {
    PickerOne,
    PickerTwo
  }
})
export default class RecommendPlanSelector extends Vue {
  private rank: number | string = 0;
  private type: number | string = 0;
  private order: number | string = 0;
  private orderData: GameData[] = [
    {
      id: 0,
      text: '准确率降序'
    },
    {
      id: 1,
      text: '准确率升序'
    },
    {
      id: 2,
      text: '当前连中升序'
    },
    {
      id: 3,
      text: '当前连中降序'
    }
  ];
  private typeData: GameData[] = [
    {
      id: 0,
      text: '定码'
    },
    {
      id: 1,
      text: '杀码'
    },
    {
      id: 2,
      text: '大小'
    },
    {
      id: 3,
      text: '单双'
    },
    {
      id: 4,
      text: '龙虎'
    }
  ];
  private rankData: GameData[] = [
    {
      id: 0,
      text: "冠军",
      children: this.typeData
    },
    {
      id: 1,
      text: "亚军",
      children: this.typeData
    },
    {
      id: 2,
      text: "第三名",
      children: this.typeData
    },
    {
      id: 3,
      text: "第四名",
      children: this.typeData
    },
    {
      id: 4,
      text: "第五名",
      children: this.typeData
    },
    {
      id: 5,
      text: "第六名",
      children: this.typeData
    },
    {
      id: 6,
      text: "第七名",
      children: this.typeData
    },
    {
      id: 7,
      text: "第八名",
      children: this.typeData
    },
    {
      id: 8,
      text: "第九名",
      children: this.typeData
    },
    {
      id: 9,
      text: "第十名",
      children: this.typeData
    },
    {
      id: 10,
      text: "冠亚和",
      children: this.typeData
    }
  ];
  private emitData() {
    this.$emit('index-change', {
      rank: this.rank,
      type: this.type,
      order: this.order,
    })
  }
  private asignRankAndType(value: GameData) {
    const id = value.id as number[];
    this.rank = id[0];
    this.type = id[1];
    this.emitData();
  }
  private asignOrder(value: GameData) {
    const id = value.id as number;
    this.order = id;
    this.emitData();
  }
}
